  .slick-next::before {
    display: none; }
  .slick-prev::before {
    display: none; }
  .slide-caption {
    padding: 0 30px 30px !important; }
  .slide-caption h1 {
    font-size: 15pt !important; } }

/** Custom media queries */
/** Custom media queries */
@media (min-width: 600px) {
  .fix-button-collaborators {
    padding: 0 2rem; }
  .hero-content {
    height: 500px; } }

@media (min-width: 600px) and (max-width: 830px) {
  .article-fix {
    width: 100% !important; }
  .fix-border-top-show {
    border-top: none; }
  .fix-padding-show {
    padding: 0; }
  .fix-indent-show {
    text-indent: 0 !important; } }

@media (max-width: 600px) {
  .slick-carousel, #share-google {
    display: none !important; }
  .border-article-left,
  .border-article-right {
    border: none; }
  .fix-border-top {
    border-top: none !important; }
  #more-reads > .category-motivapp-green, #more-reads > .category-motivapp-purple,
  #more-reads > .category-motivapp-blue, #more-reads > .category-motivapp-red {
    padding-left: 0 !important; }
  .tit-ultimo {
    padding-left: 0 !important; }
  .date {
    padding-left: 0 !important; }
  .no-padding-fix {
    padding: 0 0.75rem !important; }
  #second-text, #third-text, #fourth-text {
    padding-bottom: 20px;
    padding-top: 60px; }
  .hero-content {
    padding-bottom: 40px !important; }
  #fab-menu {
    min-height: 300px; }
  .text-padding-responsive {
    padding-left: 10px !important; } }

/** Global media queries */
@media only screen and (max-width: 360px) {
  .rating-share-icon-list li a i {
    font-size: 1.9rem; }
  .rating-share-icon-list h2 {
    font-size: 0.8rem; }
  h2 {
    font-size: 2.5rem; }
  h3 {
    font-size: 2.0rem; } }

html {
  font-family: 'SourceSansPro-Regular', sans-serif; }

/** error classes */
input[type=text].has-error,
input[type=email].has-error,
input[type=password].has-error {
  border-bottom: 1px solid #F44336;
  -webkit-box-shadow: 0 1px 0 0 #F44336;
  -moz-box-shadow: 0 1px 0 0 #F44336;
  box-shadow: 0 1px 0 0 #F44336; }

span.has-error {
  color: #F44336;
  font-size: 0.8rem; }

a {
  outline: none; }

hr {
  border: 1px solid #d7d7d7; }

/** Quick Fixes and overwrites of materialize framework */
.remove-box-shadow {
  box-shadow: none; }

.no-margin-top {
  margin-top: 0; }

.no-margin-bottom {
  margin-bottom: 0; }

.centering {
  margin: 0 auto; }

.width-100p {
  min-width: 100%; }

.bg-motivapp-purple {
  background-color: #564b98 !important; }

.bg-motivapp-red {
  background-color: #e54028 !important; }

.bg-motivapp-green {
  background-color: #91d335 !important; }

.bg-motivapp-blue {
  background-color: #00aeef !important; }

.text-motivapp-purple {
  color: #564b98 !important; }

.text-motivapp-red {
  color: #e54028 !important; }

.text-motivapp-green {
  color: #91d335 !important; }

.text-motivapp-blue {
  color: #00aeef !important; }

.preview-img {
  width: 30%;
  margin: auto;
  display: block;
  margin-bottom: 10px; }

.slide-caption {
  min-height: 80px;
  position: absolute;
  width: 100%;
  z-index: 2;
  background: rgba(51, 51, 51, 0.8);
  background: linear-gradient(180deg, rgba(244, 244, 244, 0), rgba(37, 37, 37, 0.6));
  color: white;
  font-size: 1rem;
  letter-spacing: 1px;
  line-height: 1.5rem;
  height: 50%;
  bottom: 0;
  padding: 60px 32px 35px; }

.slide-caption h1 {
  font-family: 'MuseoSlab-100',sans-serif;
  font-weight: 100;
  text-transform: uppercase;
  font-size: 26px;
  padding: 5px;
  margin: 0; }

.slide-caption p {
  line-height: 19pt;
  text-transform: uppercase;
  padding: 0 5px 5px 5px;
  margin: 0;
  display: inline-block;
  font-size: 9pt; }

.text-justify {
  text-align: justify; }
